import React, { useRef, useState, useEffect } from "react";
import { Col, Row, Container, Button, Form, Card } from "react-bootstrap";
import { navigate } from "gatsby";

import "../components/pages-css/sCita.css";
import Layout from "../components/layout.js";
import Seo from "../components/seo";
import VideoSeccion from "../components/videoSeccion.js";
import ModalEmergente from "../components/ModalEmergente.js";
import Timer from "../components/timer.js";
import api from "../components/actions/riveroapi.js";
import PlaceholderPaquete from "../components/placeholderPaquete";

export default function ServiciosAdicionales(props){
    //console.log("Tengo los props en Servicios Adicionales: ", props.location.state);

    const [show, setShow] = useState(false);
    const [modal, setModal] = useState(false);
    const [errorInput, setErrorInput] = useState(false);
    const [error, setError] = useState(0);
    const [click, setClick] = useState(false);
    const [category, setCategory] = useState({
        Proteccion: "",
        Estetica: "",
        Eliminadores: ""
    })

    const [selected, setSelected] = useState([]);

    const refForm = useRef();

    const minira = [0, 1];

    const mapPlaceholder = minira.map((obj, i) => {
        return(
            <label style={{width: '50%'}} key={i}>
                <Row>
                    <Col style={{width: '100%', height:'100%'}} lg={6}>
                        <PlaceholderPaquete/>
                    </Col>
                </Row>
            </label>
        )
    })

    useEffect(() => {
        api.getServiciosAdicionales().then((resp) => {
            setCategory({
                ...category,
                Proteccion: resp.data.categorias["PROTECCION ADICIONAL"].adicionales,
                Estetica: resp.data.categorias["ESTETICA AUTOMOTRIZ"].adicionales,
                Eliminadores: resp.data.categorias["ELIMINADORES DE MALOS OLORES / ANTIBACTERIALES"].adicionales
            })
        })
    }, [])

    function selecAdicional(data, number){

        const servicios = {
            servicio: data,
            precio: number
        };

        setSelected( nums => nums.some(item => item.servicio === data) ? nums.filter(item => item.servicio !== data) : [...nums, servicios]);
        //setSelected(nums => nums.includes(data) ? nums.filter(n => n !== data) : [data, ...nums]);

    }

    function continuar(){
        navigate("/resumen-servicios");
        const params = {
            paquetesAdicionales: selected
        }

        localStorage.setItem("adicionales", JSON.stringify(params));
    }

    return(
        <Layout backUrl={"/comentarios-paquetes"}>
            <Seo title="Auto servicio" />
            <Container className="container-sin-cita" onClick={() => {setClick(true)}}>
            {<Timer click={click} setClick={setClick} setError={setError} setModal={setModal}/>}
                <Row className="row-cita-arriba">
                    <div className="container-cita">
                        <Row>
                            <Col lg={12} className="titulo-cita"><h1 className="titulo-h1">Servicios adicionales </h1></Col>
                        </Row>
                        <Col lg={12} className="titulo-cita"><p className="texto-gris" style={{marginBottom: 5}}>Seleccione algún servicio adicional que desee agregar, esto es opcional.</p></Col>
                        
                        <Col lg={12} className="titulo-cita"><h5 style={{color: '#0d6efd'}}>Proteccion Adicional</h5></Col>
                        <Row style={{width: '100%', overflowX: 'scroll', flexWrap: 'nowrap', height: 185}}>
                            {
                                category && category.Proteccion && category.Proteccion.length > 0 ? (
                                    category.Proteccion.map((obj, index) => {

                                        return(
                                            <label style={{width: '45%', height: '95%'}} key={index}>
                                                <input class="item-check" name="keywords[]" type="checkbox" value="1" />
                                                <Col style={{ width: '100%', height: '95%' }} className="item-name">
                                                    <Card style={{ width: '100%', height: '100%' }}onClick={(e) => selecAdicional(obj.nombre, obj.precio)}>
                                                        <Card.Header style={{ fontSize: 14 }}>{obj.nombre}</Card.Header>
                                                        <Card.Body>
                                                            <Card.Text className="texto-gris" style={{fontSize: 13}}>
                                                                {obj.detalle}
                                                            </Card.Text>
                                                        </Card.Body>
                                                        <Card.Footer>
                                                            <center><small className="text-muted">$ {obj.precio} pesos</small></center>
                                                        </Card.Footer>
                                                    </Card>
                                                </Col>
                                            </label>
                                        )
                                    })
                                ): (
                                    mapPlaceholder
                                )
                            }
                        </Row>
                        
                        <Col lg={12} className="titulo-cita" style={{padding: 5}}><h5 style={{color: '#0d6efd'}}>Eliminadores de malos olores/antibacteriales</h5></Col>
                        <Row style={{width: '100%', overflowX: 'scroll', flexWrap: 'nowrap', height: 185}}>
                            {
                                category && category.Eliminadores && category.Eliminadores.length > 0 ? (
                                    category.Eliminadores.map((obj, index) => (
                                        <label style={{width: '45%', height: '95%'}} key={index}>
                                            <input class="item-check" name="keywords[]" type="checkbox" value="1" />
                                            <Col style={{ width: '100%', height: '95%' }} className="item-name">
                                                <Card style={{ width: '100%', height: '100%' }}onClick={(e) => selecAdicional(obj.nombre, obj.precio)}>
                                                    <Card.Header style={{ fontSize: 14 }}>{obj.nombre}</Card.Header>
                                                    <Card.Body>
                                                        <Card.Text className="texto-gris" style={{fontSize: 13}}>
                                                            {obj.detalle}
                                                        </Card.Text>
                                                    </Card.Body>
                                                    <Card.Footer>
                                                        <center><small className="text-muted">$ {obj.precio} pesos</small></center>
                                                    </Card.Footer>
                                                </Card>
                                            </Col>
                                        </label>
                                    ))
                                ): (
                                    mapPlaceholder
                                )
                            }
                        </Row>  
                        
                        <Col lg={12} className="titulo-cita" style={{padding: 5}}><h5 style={{color: '#0d6efd'}}>Estetica automotriz</h5></Col>
                        <Row style={{width: '100%', overflowX: 'scroll', flexWrap: 'nowrap', height: 185}}>
                            {
                                category && category.Estetica && category.Estetica.length > 0 ? (
                                    category.Estetica.map((obj, index) => (
                                        <label style={{width: '45%', height: '95%'}} key={index}>
                                            <input class="item-check" name="keywords[]" type="checkbox" value="1" />
                                            <Col style={{ width: '100%', height: '95%' }} className="item-name">
                                                <Card style={{ width: '100%', height: '100%' }}onClick={(e) => selecAdicional(obj.nombre, obj.precio)}>
                                                    <Card.Header style={{ fontSize: 14 }}>{obj.nombre}</Card.Header>
                                                    <Card.Body>
                                                        <Card.Text className="texto-gris" style={{fontSize: 13}}>
                                                            {obj.detalle}
                                                        </Card.Text>
                                                    </Card.Body>
                                                    <Card.Footer>
                                                        <center><small className="text-muted">$ {obj.precio} pesos</small></center>
                                                    </Card.Footer>
                                                </Card>
                                            </Col>
                                        </label>
                                    ))
                                ): (
                                    mapPlaceholder
                                )
                            }
                        </Row>  
                    </div>
                </Row>
                <Button ref={refForm} onClick={() => continuar()} style={{display: 'none'}}></Button>
                <Row className="row-cita-abajo">
                    <VideoSeccion refForm={refForm} none="tiene"/>
                </Row>
            </Container>
            <ModalEmergente modal={modal} error={error} setModal={setModal} setClick={setClick}/>
        </Layout>
    )

}